import { Routes , RouterModule } from '@angular/router';
import { AuthURL } from './auth.url';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { ProfileComponent } from './components/profile/profile.component';
import { RegisterEmployeeComponent } from './components/register-employee/register-employee.component';
import { RegisterCustomerComponent } from './components/register-customer/register-customer.component';


const RouteList:Routes = [
    {path:'',redirectTo:AuthURL.Dashboard,pathMatch:'full'},
    {path:AuthURL.Dashboard,component:DashboardComponent},
    {path:AuthURL.Profile,component:ProfileComponent},
    {path:AuthURL.RegisterEmployee,component:RegisterEmployeeComponent},
    {path:AuthURL.RegisterCustomer,component:RegisterCustomerComponent}
];

export const AuthRoutes = RouterModule.forChild(RouteList);